  --iconBg: #101010;
  --widgetBg: #fff;
  --exitIconColor: #101010;
  --workImgFilter: invert(0);
  --noticesAmountColor: #101010;
}

body.color_yd .header {
  --headerBg: #101010;
  --textColor: #fff;
  --textMuted: #f9ec00;
  --iconColor: #f9ec00;
  --iconBg: #101010;
  --widgetBg: #292929;
  --exitIconColor: #f9ec00;
  --workImgFilter: invert(1);
  --noticesAmountColor: #FFFFFF;
}

body.color_pl .header {
  --headerBg: #F1F4F6;
  --textColor: #101010;
  --textMuted: #A1ACB9;
  --iconColor: #ffffff;
  --iconBg: #D01181;
  --widgetBg: #fff;
  --exitIconColor: #101010;
  --noticesAmountColor: #101010;
}

body.color_pd .header {
  --headerBg: #101010;
  --textColor: #fff;
  --textMuted: #D01181;
  --iconColor: #ffffff;
  --iconBg: #D01181;
  --widgetBg: #fff;
