.header {
  position: fixed;
  top: 0;
  left: 33rem;
  right: 0;
  padding: 1.6rem 3.2rem 1.6rem 4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--headerBg);
  color: var(--textColor);
  z-index: 10;
}
.header__content {
  display: flex;
  align-items: center;
}
.header__user-info {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-right: 4.8rem;
}
.header__user-info__photo {
  width: 5.7rem;
  height: 5.7rem;
  border-radius: 100%;
  overflow: hidden;
}
.header__user-info__photo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.header__user-info__desc {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.header__user-info__name {
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 93.75%;
}
.header__user-info__job {
  color: var(--textMuted);
}
.header__work {
  background-color: var(--widgetBg);
  border-radius: 3.2rem;
  margin-right: 1.6rem;
  cursor: pointer;
  height: 5rem;
  width: 17.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__work__logo img {
  max-width: 12.6rem;
  filter: var(--workImgFilter);
}
.header__notices {
  background-color: var(--widgetBg);
  border-radius: 3.2rem;
  padding: 0.7rem 0.8rem 0.7rem 1.8rem;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: normal;
  display: flex;
  align-items: center;
  gap: 3.4rem;
  cursor: pointer;
}
.header__notices__ico {
  background-color: var(--iconBg);
  padding: 1.2rem 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
}
.header__notices__ico svg {
  width: 1.3rem;
  height: 1.3rem;
}
.header__notices__ico svg path {
  fill: var(--iconColor);
}
.header__notices__amount {
  color: var(--noticesAmountColor);
}
.header__exit svg {
  width: 2.4rem;
  height: 2.4rem;
}
.header__exit svg path {
  stroke: var(--exitIconColor);
}

