  width: 2.4rem;
  height: 2.4rem;
  justify-content: center;
  align-items: center;
}
.incoming-requests__item__details .request-archive svg {
  width: 1.8rem;
  height: 1.8rem;
}
.incoming-requests__item__details .request-archive svg path {
  stroke: var(--itemColor);
}
.incoming-requests__item__title {
  font-size: 1.8rem;
  line-height: 120%;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  font-weight: 500;
}
.incoming-requests__item__title--row {
  display: none;
}
.incoming-requests__item__subtitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.incoming-requests__item__subtitle .request-category {
  color: #acacac;
}
.incoming-requests__item__subtitle .request-status {
  border-radius: 1.6rem;
  padding: 0.4rem 1.2rem;
  letter-spacing: 0.014rem;
  font-size: 1.4rem;
  white-space: nowrap;
}
.incoming-requests__item__subtitle .request-status.agreed {
  background-color: var(--statusAgreedBg);
  color: var(--statusAgreedText);
}
.incoming-requests__item__subtitle .request-status.rejected {
  color: #fff;
  background: #bb2323;
}
.incoming-requests__item__subtitle .request-status.waiting {
  background-color: var(--statusWaitingBg);
  color: var(--statusWaitingText);
}
.incoming-requests__item__btn {
  margin-top: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--btnBg);
  padding: 0.65rem 0.7rem 0.65rem 1.6rem;
  border-radius: 3.2rem;
  border: var(--btnBorder);
}
.incoming-requests__item__btn svg {
  width: 3.6rem;
  height: 3.6rem;
}
.incoming-requests__item__btn span {
  color: #101010;
}

body.color_pl .incoming-requests__box--unread .star-ico {
  --starStroke: #ffffff;
}

.incoming-requests ~ #ui-datepicker-div {
  display: none !important;
}

body .request-details {
  --textColor: #101010;
  --statusWaitingBg: #d7d7d7;
  --statusWaitingText: #101010;
  --statusAgreedBg: #f9ec00;
  --statusAgreedText: #101010;
  --btnShareBg: #101010;
  --btnShareStroke: #ffffff;
  --btnCallBg: #f9ec00;
  --btnCallStroke: #101010;
  --inputBg: #ffffff;
  --placeholderColor: #ACACAC;
  --btnCancelBg: #d7d7d7;
  --btnCancelText: #101010;
  --inputBorder: none;
  --inputTitleColor: #101010;
}

body.color_yd .request-details {
  --textColor: #ffffff;
  --statusWaitingBg: #d7d7d7;
  --statusWaitingText: #101010;
  --statusAgreedBg: #f9ec00;
  --statusAgreedText: #101010;
  --btnShareBg: #292929;
  --btnShareStroke: #ffffff;
  --btnCallBg: #f9ec00;
  --btnCallStroke: #101010;
