  --btnCancelBg: #ffffff;
  --btnCancelText: #101010;
  --inputBorder: none;
  --inputTitleColor: rgba(255, 255, 255, 0.80);
}

body.color_pl .request-details {
  --btnCallBg: #D01181;
  --btnCallStroke: #ffffff;
  --btnCancelBg: #A1ACB9;
  --btnCancelText: #ffffff;
  --btnShareBg: #101010;
  --btnShareStroke: #ffffff;
  --inputTitleColor: #101010;
  --statusAgreedBg: #D01181;
  --statusAgreedText: #ffffff;
}

body.color_pd .request-details {
  --textColor: #ffffff;
  --inputBg: #101010;
  --inputBorder: 0.1rem solid rgba(255, 255, 255, 0.20);
  --inputTitleColor: rgba(255, 255, 255, 0.80);
  --btnCallStroke: #ffffff;
  --btnCancelBg: #ffffff;
  --btnCancelText: #101010;
  --btnShareBg: #222427;
  --btnShareStroke: #ffffff;
  --btnCallBg: #D01181;
  --btnCallStroke: #ffffff;
  --statusAgreedBg: #D01181;
