}

.request-details {
  color: var(--textColor);
}
.request-details .main__title .status {
  border-radius: 1.6rem;
  padding: 0.4rem 1.2rem;
  letter-spacing: 0.014rem;
  font-size: 1.4rem;
  white-space: nowrap;
}
.request-details .main__title .status.agreed {
  color: var(--statusAgreedText);
  background-color: var(--statusAgreedBg);
}
.request-details .main__title .status.waiting {
  color: var(--statusWaitingText);
  background-color: var(--statusWaitingBg);
}
.request-details .main__title .status.rejected {
  color: #fff;
  background: #BB2323;
}
.request-details__content {
  display: flex;
  gap: 2rem;
}
.request-details__form {
  flex: 1;
  padding: 3.2rem 2.4rem;
  height: fit-content;
}
.request-details__form__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}
.request-details__form__top .title {
  font-size: 2.4rem;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: 0.024rem;
}
.request-details__form__top .btns {
  display: flex;
  gap: 2rem;
}
.request-details__form__top .btns .btn__share,
.request-details__form__top .btns .btn__call {
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 5.1rem;
  height: 5.1rem;
}
.request-details__form__top .btns .btn__share svg,
.request-details__form__top .btns .btn__call svg {
  width: 2rem;
  height: 2rem;
}
.request-details__form__top .btns .btn__share {
  background-color: var(--btnShareBg);
}
.request-details__form__top .btns .btn__share svg path {
  stroke: var(--btnShareStroke);
}
.request-details__form__top .btns .btn__call {
  background-color: var(--btnCallBg);
}
.request-details__form__top .btns .btn__call svg path {
  stroke: var(--btnCallStroke);
}
.request-details__form__inputs {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  width: 100%;
  margin-bottom: 4rem;
}
.request-details__form__input {
  display: flex;
  gap: 3.2rem;
  align-items: center;
}
.request-details__form__input .title {
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 93.75%;
  width: 19.2rem;
  color: var(--inputTitleColor);
}
.request-details__form__input .input-wrapper {
  padding: 1.8rem 1.6rem;
  background-color: var(--inputBg);
  border: var(--inputBorder);
  flex: 1;
  border-radius: 1.6rem;
  font-weight: 500;
  line-height: 120%;
}
.request-details__form__input input {
  width: 100%;
}
.request-details__form__input input::placeholder {
  color: var(--placeholderColor);
}
.request-details__form__input--textarea {
  align-items: start;
}
.request-details__form__input--textarea textarea {
  height: 13.9rem;
  width: 100%;
  resize: none;
}
.request-details__form__input--textarea textarea::placeholder {
  color: var(--placeholderColor);
}
.request-details__form__input--file {
  align-items: start;
}
.request-details__form__input--file .input-wrapper {
  display: flex;
  gap: 1rem;
}
.request-details__form__input--file__file {
  width: 8rem;
  height: 8rem;
  border-radius: 0.8rem;
  overflow: hidden;
}
.request-details__form__input--file__file img {
  width: 100%;
  height: 100%;
}
.request-details__form__btns {
  display: flex;
  gap: 1.2rem;
  align-items: center;
  width: 100%;
  margin-left: auto;
}
.request-details__form__btns .btn__cancel {
  margin-left: auto;
  background-color: var(--btnCancelBg);
  color: var(--btnCancelText);
}
.request-details__form__btns .btn__accept,
.request-details__form__btns .btn__cancel {
  width: 20rem;
  padding: 1.6rem 6rem;
}
.request-details__img {
  height: 83.7rem;
  width: 29rem;
  border-radius: 1.6rem;
  overflow: hidden;
}
.request-details__img img {
  width: 100%;
  height: 100%;
}

body .login {
  --bgRight: #292929;
  --bgLeft: #101010;
  --text: #fff;
  --textMuted: #acacac;
  --textAccent: #f9ec00;
  --inputText: #101010;
}

.login {
  margin: 0;
  height: 100vh;
  width: 100vw;
  background-color: var(--bgRight);
  color: var(--text);
}
.login .container {
  padding: 0;
  display: flex;
  width: 100%;
  height: 100%;
}
.login__left {
  background-color: var(--bgLeft);
  width: 79.5rem;
  padding: 3rem 4rem;
  background-image: url("./../../assets/images/common/sidebar-deco-big.svg");
  background-position: bottom left;
  background-repeat: no-repeat;
}
.login__right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.login__logo {
  width: 17.5rem;
  height: 2.9rem;
}
.login__logo img {
  width: 100%;
  height: 100%;
}
.login__info {
  margin-top: 10.7rem;
  margin-left: 4rem;
  width: 41.1rem;
}
.login__info__title {
  font-size: 6rem;
  font-weight: 700;
}
.login__info__line {
  margin-top: 6rem;
  margin-bottom: 6rem;
  height: 0.2rem;
  background-color: var(--text);
  width: 16rem;
}
.login__info__desc {
  letter-spacing: 0.016rem;
  line-height: normal;
  color: var(--textMuted);
}
.login__info a {
  color: var(--textAccent);
}
.login__form {
  width: 47.7rem;
}
.login__form__title {
  color: var(--text);
  font-size: 3.2rem;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: 0.032rem;
  margin-bottom: 5.2rem;
  text-align: center;
}
.login__form__inputs {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  margin-bottom: 4rem;
}
.login__form .input-wrapper {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  letter-spacing: 0.016rem;
}
.login__form .input-wrapper span {
  color: var(--textAccent);
}
.login__form .input-wrapper input {
  height: 5.4rem;
  background-color: var(--text);
  border-radius: 1.6rem;
  padding: 1.8rem 1.6rem;
  color: var(--inputText);
}
.login__form .input-wrapper input::placeholder {
  color: var(--textMuted);
}

