  --textMuted: #A1ACB9;
  --addNewsBtnBg: #D01181;
}

body.color_pd .news {
  --textColor: #FFFFFF;
  --addNewsBg: #f4f4f4;
  --addNewsText: #101010;
  --addNewsIconStroke: #ffffff;
  --cardBg: #f1f4f6;
  --cardText: #101010;
  --textMuted: #A1ACB9;
  --addNewsBtnBg: #D01181;
}

.news {
  color: var(--textColor);
}
.news__content {
  width: 122rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.news .btn__add-news {
  margin-left: auto;
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 0.5rem 0.5rem 0.5rem 1.2rem;
  border-radius: 5.2rem;
  background-color: var(--addNewsBg);
  color: var(--addNewsText);
}
.news .btn__add-news span {
  letter-spacing: 0.016rem;
  font-size: 1.6rem;
  font-weight: 400;
}
.news .btn__add-news svg {
  width: 4rem;
  height: 4rem;
}
.news .btn__add-news svg path {
  stroke: var(--addNewsIconStroke);
}
.news .btn__add-news svg circle {
  fill: var(--addNewsBtnBg);
}
.news__card {
  background-color: var(--cardBg);
  color: var(--cardText);
  border-radius: 3.2rem;
  overflow: hidden;
  letter-spacing: 0.016rem;
  display: flex;
}
.news__card__swiper {
  width: 60.2rem;
  position: relative;
}
.news__card__swiper img {
  width: 100%;
  height: 100%;
}
.news__card__swiper .swiper-pagination {
  z-index: 100;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translate(-50%);
  width: 4rem;
  height: 0.8rem;
  display: flex;
  gap: 0.8rem;
}
.news__card__swiper .swiper-pagination .swiper-pagination-bullet {
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 100%;
  background-color: #D7D7D7;
}
.news__card__swiper .swiper-pagination .swiper-pagination-bullet-active {
  background-color: #101010;
}
.news__card__content {
  padding: 2.4rem;
  color: var(--cardText);
  width: 61.8rem;
}
.news__card__date {
  color: var(--textMuted);
  margin-bottom: 0.8rem;
}
.news__card__title {
  letter-spacing: 0.024rem;
  font-weight: 500;
  line-height: 100%;
  margin-bottom: 1.2rem;
}
.news__card__link {
  position: relative;
  display: inline-block;
  margin-bottom: 0.8rem;
}
.news__card__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: var(--cardText);
  height: 0.1rem;
}
.news__card__about {
  padding-left: 0.6rem;
  position: relative;
  margin-bottom: 1.2rem;
  display: flex;
  gap: 1.8rem;
}
.news__card__about::after {
  content: "";
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--cardText);
  width: 0.1rem;
}
.news__card__about__info {
  width: 33.1rem;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}
.news__card__about__info .link,
.news__card__about__info .title {
  font-weight: 500;
}
.news__card__about__img {
  width: 9.4rem;
  height: 5.3rem;
  border-radius: 0.8rem;
  overflow: hidden;
}
.news__card__about__img img {
  width: 100%;
  height: 100%;
}
.news__card__desc {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 1.2rem;
}
.news__card__desc span {
  display: block;
}
.news__card__desc.open {
  -webkit-line-clamp: unset;
}
.news__card__toggle {
  display: inline-block;
  position: relative;
}
.news__card__toggle::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  background-color: var(--cardText);
  height: 0.1rem;
}

.add-news .request-details__img {
  height: unset;
